@import url(https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
body {
  background: url(https://firebasestorage.googleapis.com/v0/b/tienda-gaijin.appspot.com/o/bg.jpg?alt=media&token=f04d57bd-7fc1-4fb3-a124-db5d9be8f1e3) no-repeat center center fixed;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  margin-top: 10vw;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.off{
  color: blue;
}

.background, .tableDark, .tableDetail {
  border: white solid thick;
  background-color: rgba(0, 0, 0, 0.6);
  font-family: "Poppins";
  margin: 2%;
  padding: 10px;
  color: white;
  width: auto; 
  border-radius: 10px;
}

.container{
  display: flex;
  flex-flow: column; 
  align-items: center;
}

img{
  border-radius: 10px;
}

button:hover{
  -webkit-filter:brightness(120%);
          filter:brightness(120%)
}

button:active{
  -webkit-filter:brightness(70%);
          filter:brightness(70%);
}

.NavBar {
    display: flex;
    justify-content: space-around;
    font-size: 2vw;
    font-family: "Poppins";
    position: fixed;
    width: 96%;
    top:2px;
    left: 1.5%;
    border: black solid thick;
    background: grey;
    border-radius: 10px;
    z-index: 10;
}

.NavLink, .off, a, #Cart{
    color: black;
    flex-grow: 1;
    cursor: pointer;
    flex-shrink: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
}

.off{
    color: darkblue;
}
.off:hover{
    color: lightblue;
}

h1 {
    color: white;
    font-size: 3vw;
    font-family: "Permanent Marker";
}

b{
    color: rgb(188,0,45);
    font-size: 1.5vw;
}


#Cart{
    background-color: lightblue;
    border-radius: 0 5px 5px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4vw;
}

.NavLink:hover, #Cart a:hover{
    color: white;
    background-color: black;
}
#ItemListContainer{
    display: flex;
    justify-content: space-evenly;
}
b{
    font-size: 9vw;
}
img{
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%; 
}

.tableDark {
    border-collapse: collapse;
    color: white;
    display: flex;
    flex-wrap: wrap;
    border-radius: 10px;
}

.subTable{
    display: flex;
    border: 2px solid #a0a0a0; 
    min-width: 18%;
    flex-flow: column;
    padding: 1.3%;
    justify-content: space-between;
    margin: 1%;
    border-radius: 10px;
    flex-grow: 1;
    max-width: 28%;
}

.tableDark div img {
    width: 100%; 
}

b {
    color: #bc002d;
    font-family: "Permanent Marker";
    font-size:  1rem;
}

button{
    width: 10px;
}
.tableDetail{
    display: flex;
    max-width: 100%;
    border: white thick solid;
    flex-flow: row;
}

.tableDark div img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%; 
}

b {
    color: #bc002d;
    font-family: "Permanent Marker";
    font-size:  2vw;
    text-shadow: 2px 2px 8px white , 0 0 10px black;;
}

p {
    font-family: "Poppins";
    font-size: 1vw;
}

button{
    width: 10px;
}
#pictureContainer{
    height: 30vw;
}
#totalCost{
    font-size: 2rem;
    justify-content:space-between; 
    width: 15%;
    margin: 1%;
    background-color: #bc002c93;
}
fieldset{
    display: flex;
}
fieldset div, fieldset input, textarea{
    min-width: 50%;
}
fieldset input{
    height: 2rem;
}
.modal{
    position: fixed;
    top: 50%;
    left: 33.3333%;
    z-index: 1060;
    display: none;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    outline: 0;
    justify-content: center;
}

.modal-content {
    position: relative;
    display: flex;
    flex-direction: column;
    pointer-events: auto;
    background-clip: padding-box;
    outline: 0;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    border: #bc002d solid thick;
    background-color: rgba(255, 255, 255, 0.8);
    font-family: "Poppins";
    margin: 2%;
    padding: 10px;
    color: black;
    border-radius: 10px;
    font-weight: bolder;
    font-size: 2rem;
    text-align: center;
}
.modal-dialog{
    width: 33.33333%;
    height: 200px;
}
.modal button{
    position: absolute;
    top: 2px;
    right: 2px;
    border-radius: 100% !important;
}


