.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.off{
  color: blue;
}

.background, .tableDark, .tableDetail {
  border: white solid thick;
  background-color: rgba(0, 0, 0, 0.6);
  font-family: "Poppins";
  margin: 2%;
  padding: 10px;
  color: white;
  width: auto; 
  border-radius: 10px;
}

.container{
  display: flex;
  flex-flow: column; 
  align-items: center;
}

img{
  border-radius: 10px;
}

button:hover{
  filter:brightness(120%)
}

button:active{
  filter:brightness(70%);
}